.header__contacts .phone::before,
.header__contacts .work-time::before,
.header__contacts .email::before,
.header__contacts .place::before {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 127px 102px;
}

.header {
  z-index: 49;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  flex: 0 0 auto;
  height: 110px;
  color: #000;
}

.header::before {
  z-index: 50;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #FFF;
}

.header__inner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header__logo {
  z-index: 51;
  color: #005fa3;
  font-family: 'Merriweather';
  font-size: 36px;
  font-weight: 900;
  line-height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.header__info {
  flex: 0 0 auto;
  width: 660px;
}

.header__contacts {
  z-index: 51;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-bottom: 10px;
}

.header__contacts .phone {
  padding-left: 20px;
}

.header__contacts .phone::before {
  top: 30%;
  transform: rotate(90deg);
  width: 11px;
  height: 7px;
  background-position: -105px -59px;
}

.header__contacts .work-time::before {
  width: 18px;
  height: 18px;
  background-position: -105px -5px;
}

.header__contacts .email::before {
  width: 24px;
  height: 18px;
  background-position: -5px -55px;
}

.header__contacts .place::before {
  width: 18px;
  height: 18px;
  background-position: -105px -32px;
}

.header__contacts a,
.header__contacts span {
  font-size: 13px;
  line-height: 17px;
  font-weight: 400;
  color: #000;
  border-color: transparent;
}

.header__burger {
  z-index: 51;
  display: none;
  cursor: pointer;
}

.header__burger span {
  position: absolute;
  background-color: #000;
  width: 100%;
  height: 2px;
  left: 0;
  top: 9px;
  transform: scale(1);
  transition: 0.3s all;
}

.header__burger.active::before {
  transform: rotate(45deg);
  top: 9px;
}

.header__burger.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
}

.header__burger.active span {
  transform: scale(0);
}

.logo {
  width: 40px;
  height: 40px;
  margin-right: 30px;
}

.link-icon {
  padding-left: 30px;
  position: relative;
}

.link-icon::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translate3d(0, -50%, 0);
}

@media only screen and (max-width: 991px) {
  .header__info {
    width: auto;
  }

  .header__contacts {
    display: none;
  }

  .header .link-icon {
    margin-bottom: 3px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
  }

  .header__burger::before,
  .header__burger::after {
    content: "";
    position: absolute;
    background-color: #000;
    width: 100%;
    height: 2px;
    left: 0;
    transition: 0.3s all;
  }

  .header__burger::before {
    top: 0;
  }

  .header__burger::after {
    bottom: 0;
  }
}